export default {
	data() {
		return {
			cityName:'北京'
		};
	},
	mounted() {
		uni.getStorage({
			key:'city',
			success:(res)=>{
				if(res.data.length>0){
					this.cityName = res.data
				}
			}
		})
	},
	watch:{
		cityName(nval,oval){
			console.log(nval,oval);
		}
	}
}
